body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#fafafa}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.input-todo{display:flex;justify-content:space-between;background-color:#fff;box-shadow:4px 4px 10px 0px rgba(0,0,0,0.2);width:500px;padding:10px;border-radius:10px}.input-todo input{width:100%;border:none;outline:none;font-family:"Outfit",sans-serif;font-size:1.5em}.input-todo input:-ms-input-placeholder{opacity:0.5}.input-todo input::placeholder{opacity:0.5}

.todo-item{display:flex;width:500px;align-items:center;background-color:#fff;border-bottom:1px solid #d3d3d3;padding:10px;border-radius:10px}.todo-item input[type="checkbox"]{cursor:pointer}.todo-item input[type="checkbox"]{margin:0;-webkit-appearance:none;width:20px;height:20px;border:1px solid #d3d3d3;border-radius:50%}.todo-item input[type="checkbox"]:checked{background:linear-gradient(to bottom right, blue, pink)}.todo-item input[type="checkbox"]:checked+span{text-decoration:line-through}.todo-item input[type="checkbox"]:hover{opacity:0.6}.todo-item span{margin-left:10px;font-size:1.5em;font-weight:300;color:#535355;cursor:default}

.todo-list{margin-top:25px;border-radius:10px;background-color:#fff;box-shadow:4px 4px 10px 0px rgba(0,0,0,0.2)}

.action-panel{display:flex;height:60px;padding:10px;width:500px;justify-content:space-between}.action-panel button{color:rgba(0,0,0,0.4);border:none;background-color:#fff;font-family:"Outfit",sans-serif;font-weight:600}.action-panel button:hover{cursor:pointer;opacity:0.6}

.app{display:flex;flex-direction:column;align-items:center;margin-top:70px;font-family:"Outfit",sans-serif}.app h1{width:500px;font-weight:600;font-size:50px;text-transform:uppercase;color:#f2f2f2;margin-bottom:70px}

.background-image{position:absolute;top:0;width:100%;height:300px;object-fit:cover;z-index:-1}.background-image-overlay{position:absolute;top:0;width:100%;height:300px;z-index:-1;background:linear-gradient(to bottom right, blue, pink);opacity:0.6}

